@import "https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: url("sky.6531813f.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

li {
  padding: 10px;
  list-style: none;
}

img {
  max-width: 100%;
  max-height: 100%;
}

footer {
  margin-top: 40px;
}

header {
  transition: all .3s;
}

header:hover {
  clip-path: polygon(0% 0%, 100% 0%, 100% 75%, 64% 75%, 50% 99%, 35% 75%, 0% 75%);
}

p {
  margin: 3% 5%;
}

.appa {
  height: 200px;
  width: 200px;
  animation-name: appa;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  position: relative;
}

@keyframes appa {
  0% {
    top: 0;
  }

  50% {
    top: 50px;
  }

  100% {
    top: 0;
  }
}

.card {
  height: 30vh;
  max-height: 30vh;
  width: 70vw;
  background-color: #fff6;
  border-radius: 30px;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  margin-top: 40px;
  display: flex;
}

.imagen {
  margin-left: 10px;
}

.imgElemento {
  width: 25vw;
  height: 25vh;
  border-radius: 30px;
}

.imgDestacada {
  width: 100vw;
  height: 50vh;
}

.imgParrafo {
  width: 50vw;
}

.descripcion {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  display: flex;
}

.descripcion h1 {
  margin-bottom: 30px;
}

#portada {
  overflow: hidden;
}

#logo {
  max-height: 100%;
  height: 12vh;
  grid-column: 2 / 3;
  margin: 0 auto;
  top: 50%;
  bottom: 50%;
}

#imgLogo {
  height: 100%;
  width: 100%;
  max-width: 100%;
  max-height: 100%;
}

#mainDiv {
  height: 100vh;
  width: 100vw;
  grid-template-rows: 10% 10% 10% 10% 10% 10% 10% 10% 10% 10%;
  grid-template-columns: 10% 10% 10% 10% 10% 10% 10% 10% 10% 10%;
  display: grid;
}

#navegation {
  width: 100vw;
  height: 15vh;
  background-color: #fff6;
  grid-template-columns: 40% 20% 40%;
  justify-content: center;
  align-items: center;
  font-family: Abril Fatface, cursive;
  font-size: 20px;
  display: flex;
}

#navegation a {
  color: #000;
  text-decoration: none;
}

#menu1 {
  width: 40%;
  flex-direction: row;
  justify-content: center;
  display: flex;
}

#menu2 {
  width: 40%;
  justify-content: center;
  display: flex;
}

#listaMenu {
  flex-direction: row;
  display: flex;
}

#templo {
  height: 100vh;
  width: 100%;
  min-width: 1000px;
}

#imgTemplo {
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: absolute;
  bottom: -140px;
  left: 12vw;
  right: 1vw;
}

#firstAppa {
  height: 200px;
  width: 200px;
  grid-area: 5 / 4 / 6 / 5;
  transform: scaleX(-1);
}

#secondAppa {
  height: 200px;
  width: 200px;
  grid-area: 8 / 2 / 9 / 3;
  animation-delay: 1s;
  transform: scaleX(-1);
}

#thirdAppa {
  height: 200px;
  width: 200px;
  grid-area: 3 / 8 / 4 / 9;
  animation-delay: .5s;
  transform: scaleX(-1);
}

#fourthAppa {
  height: 200px;
  width: 200px;
  grid-area: 7 / 7 / 8 / 8;
  animation-direction: reverse;
  animation-delay: .3s;
}

#contentDiv {
  flex-direction: column;
  align-items: center;
  display: flex;
  overflow: hidden;
}

#contentDiv a {
  color: #000;
  text-decoration: none;
}

#contentDiv2 {
  text-align: center;
  margin-top: 40px;
  margin-left: 20%;
  margin-right: 20%;
}

#contentDiv2 h1 {
  margin: 5%;
}

#animacionLogo {
  height: 30vh;
  width: 30vw;
  transition: width 4s, height 4s;
  position: relative;
}

#animacionLogo:hover {
  width: 50vh;
  height: 50vh;
}

